
.background {
  background-image: -webkit-gradient(linear, left top, left bottom, from(#111), color-stop(100%, rgba(0, 255, 0, 0)), color-stop(30%, transparent));
  background-image: linear-gradient(to bottom, #111, rgba(0, 255, 0, 0) 100%, transparent 30%);
}
.background:before {
  content: "";
  background: url('https://source.unsplash.com/random') no-repeat center center fixed;
  position: fixed;
  left: -5%;
  right: -5%;
  top: -5%;
  z-index: -1;
  display: block;
  background-size: 4000px;
  width: 200%;
  height: 140%;

  -webkit-filter: blur(5px);
  -moz-filter: blur(5px);
  -o-filter: blur(5px);
  -ms-filter: blur(5px);
  filter: blur(5px);
}
.fill-white {
  fill: white !important;
}
#app {
  width: 100%;
  font-family: 'Helvetica-Neue', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #fff;
  padding-bottom: 0px;
}
img {
  fill: green !important;
  height: 10px;
  width: 10px;
  padding-right: 3px;
}
.custom-font-size {
  font-size: 46px;
}
@media only screen and (max-width: 500px) {
.custom-font-size {
    font-size: 36px;
}
}

/*
  Code (css snippet) below originally sourced from: 
  https://stackoverflow.com/questions/33454533/cant-scroll-to-top-of-flex-item-that-is-overflowing-container
*/
.modal-container {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
}
.modal-container .modal-window {
  display: -ms-flexbox;
  display: -webkit-box;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
          flex-direction: column;
  -ms-flex-direction: column;
  -ms-flex-align: center;
  -ms-flex-pack: center;
  height: 100%;
}
.modal-container .modal-window .modal-content {
  width: 100%;
  padding: 10px;
  margin: auto;
}


